import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Tab, Nav } from 'react-bootstrap';
import {
    Row,
    Col,
    Card,
    Button
} from "react-bootstrap";
import Aux from "../../hoc/_Aux";
import PoitionColis from './Position.Colis'
import FlatList from 'flatlist-react';
var urlbackend = require("../../env/env")();
const divStyle = {
    "overflow-y": 'scroll',
    "border": '1px solid ',
    "height": '1000px',
    "with": "100%",
    "position": 'relative'
};
function ListTicket() {
    const [ticket, setticket] = useState([])
    const [ticketraite, setticketraite] = useState([])

    const getticket = () => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.get(urlbackend + "/ticket/list?current_page=1&per_page=500", config).then((response) => {
            setticket(response.data.data.data.filter(e => e.status == 0))
            setticketraite(response.data.data.data.filter(e => e.status !== 0))
            console.log(response.data.data.data)

        }).catch(() => {
            console.log("'eeh")
        })
    }
    const renderTicket = (person, idx) => {
        return (
            <Col md={4}>
                <Card  >
                    <Card.Title> {person.code}</Card.Title>
                    <Card.Body>{person.type}


                        <p style={{ fontWeight: "bold" }} >{person.message}</p>
                        <PoitionColis id={person.colis} />

                    </Card.Body>
                </Card>
            </Col>
        );
    }
    useEffect(() => {

        getticket()

    }, [])

    const updateicket = (id) => {
        console.log(id)
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(urlbackend + "/ticket/close/" + id, config).then((response) => {
            getticket()

        }).catch(() => {
            console.log("'eeh")
        })

    }
    const validerTickey = (id) => {

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(urlbackend + "/ticket/valid/" + id, config).then((response) => {
            getticket()

        }).catch(() => {
            console.log("'eeh")
        })

    }

    const setcolor = (id) => {
        switch (id) {
            case "0":
                return "#d8d9e1"
            case "1":
                return "#c5e89d"
            case "2":
                return "#e29de8"
            default:
                break;
        }

    }
    return (

            <Aux>
                <Tab.Container defaultActiveKey="home">
                    <Row>
                        <Col sm={2}>
                            <Nav variant="pills" className="flex-column">
                                <Nav.Item>
                                    <Nav.Link eventKey="home">Ticket en cours</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="profile">Ticket Traités</Nav.Link>
                                </Nav.Item>
                              {/*   <Nav.Item>
                                    <Nav.Link eventKey="contact">CONTACT</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="settings">SETTINGS</Nav.Link>
                                </Nav.Item> */}
                            </Nav>
                        </Col>
                        <Col sm={10}>
                            <Tab.Content>
                                <Tab.Pane eventKey="home">
                                    <Row style={{ height: "1000px" }}>
                                        <Col md={12} style={divStyle}>
                                            <Row >

                                                {ticket.map(ele => (
                                                    <Col md={3}>
                                                        <Card style={{ backgroundColor: setcolor(ele.priority) }} >
                                                            <Card.Title style={{ fontWeight: "bold", color: "#000000" }}> {ele.code} colis : {ele?.colis.index_colis} </Card.Title>
                                                            <Card.Body style={{ fontWeight: "bold", color: "#000000" }}>{ele.type}


                                                                <p style={{ fontWeight: "bold", color: "#000000" }} >{ele.message}</p>
                                                                <p style={{ fontWeight: "bold", color: "#000000" }} >{ele.colis.index_colis}</p>
                                                                <div>
                                                                    <PoitionColis id={ele.colis} />
                                                                    <Button style={{ backgroundColor: "#8c948c" }} onClick={() => updateicket(ele.id)} > fermer </Button>
                                                                    <Button style={{ backgroundColor: "#8c948c" }} onClick={() => validerTickey(ele.id)} > Valider </Button>
                                                                </div>
                                                            </Card.Body>
                                                        </Card>
                                                    </Col>

                                                ))}
                                            </Row>

                                        </Col>




                                    </Row>
                                </Tab.Pane>
                                <Tab.Pane eventKey="profile">
                                    <Row style={{ height: "1000px" }}>
                                        <Col md={12} style={divStyle}>
                                            <Row >

                                                <FlatList
                                                    list={ticketraite}
                                                    renderItem={renderTicket}
                                                    renderWhenEmpty={() => <div>List is empty!</div>}
                                                    sortBy={["code", { key: "lastName", descending: true }]}

                                                />
                                            </Row>

                                        </Col>




                                    </Row>
                                </Tab.Pane>

                            </Tab.Content>
                        </Col>
                    </Row>
                </Tab.Container>
            </Aux>



    );
}

export default ListTicket;

